import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { HttpService } from '../http.service';
import { IAccount } from '../account';
import { IMedia } from '../media';
import { IList } from '../list';

@Component({
  selector: 'app-account',
  templateUrl: './account.component.html',
  styleUrls: ['./account.component.scss']
})
export class AccountComponent implements OnInit {

  account:IAccount;
  favorites:number = 0;
  lists:number = 0;

  constructor(
    private router: Router,
    private httpService: HttpService
  ) { }

  ngOnInit() :void{
    this.httpService.mdbAccountDetails().subscribe(
      (content : IAccount) =>{
        this.account = content;
      }
    )

    this.httpService.mdbFavorites('movies').subscribe(
      (content:IMedia) =>{
        this.favorites += content.results.length;
      }
    )

    this.httpService.mdbFavorites('tv').subscribe(
      (content:IMedia) =>{
        this.favorites += content.results.length;
      }
    )

    this.httpService.mdbLists().subscribe(
      (content:IList) =>{
        this.lists = content.results.length;
      }
    )


  }

  logOut(): void{
    localStorage.removeItem('expDate');
    this.router.navigateByUrl('login');
  }

}
