import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { IMediaDetails } from '../mediaDetails';
import { HttpService } from '../http.service';
import { RouterExtService } from '../router-ext.service';
import { IAccount } from '../account';
import { IMedia } from '../media';
import { ListResult, IList } from '../list';

@Component({
  selector: 'app-details',
  templateUrl: './details.component.html',
  styleUrls: ['./details.component.scss']
})
export class DetailsComponent implements OnInit {

  errorMessage='';
  type:string;
  mediaID:string;
  genres:string;
  companies:string;
  content:IMediaDetails;
  isFavorite:boolean;
  isGuest:boolean;
  opened:boolean = false;
  lists:ListResult[] = [];
  msg='';
  msgOpened:boolean = false;

  constructor(
    private route: ActivatedRoute,
    private httpService: HttpService,
    private routerExtService: RouterExtService,
    private router: Router
  ) { }

  ngOnInit() :void{
    this.type = this.route.snapshot.paramMap.get('type');
    this.mediaID = this.route.snapshot.paramMap.get('id');

    if(localStorage.getItem('userType')=='guest'){
      this.isGuest = true;
    }
    else{
      this.isGuest = false;
    }

    if(this.isGuest){
      let data = {
        "media_type": this.type,
        "media_id": this.mediaID,
        }
      let fav = JSON.parse(localStorage.getItem(`localFav${data.media_type}`))
      if(fav != [] && fav != null){
          for(var i = 0; i < fav.length; i++) {
              if(fav[i].media_id == data.media_id) {
                  this.isFavorite = true;   
                  break;
              }
          }
      }
    } else {
      if(this.type=='movie' || this.type =='tv'){
        let type = this.type;
        if(type=='movie'){type+='s'}
        this.httpService.mdbFavorites(type).subscribe(
          (content:IMedia) =>{
            this.isFavorite = this.httpService.isFavorite(parseInt(this.mediaID));
          }
        )
      }
    }

  this.getDetails();
  this.checkList();
  }

  getDetails():void{
    this.httpService.mdbMediaDetails(this.type, this.mediaID).subscribe(
      (content:IMediaDetails) => {
        this.content = content;
        if(this.type=='tv'){
          if(this.content.production_companies.length > 0){
            let c = '';
            this.content.production_companies.forEach(element => {
              c += element.name + ', '
            }); 
            this.companies = c.substring(0,c.length -2);
          }
          if(this.content.genres.length > 0){
            let g = '';
            this.content.genres.forEach(element => {
              g += element.name + ', ';
            });
            this.genres=g.substring(0,g.length -2);
          }
        }
      },
      error => this.errorMessage = <any>error
    );
  }

  checkList():void{
    this.httpService.mdbLists().subscribe(
      (content:IList) =>{
        this.httpService.mdbCheckIfInList(content, this.mediaID);
      }
    )
  }

  goBack():void{
    let previous = this.routerExtService.getPreviousUrl();
    if(previous)
      this.router.navigateByUrl(previous);
  }

  toggleFavorite():void{
    let data;
    if(this.isFavorite){
        this.isFavorite = false;
        data = {
        "media_type": this.type,
        "media_id": this.mediaID,
        "favorite":false
        }
    }
    else{
        this.isFavorite = true;
        data = {
        "media_type": this.type,
        "media_id": this.mediaID,
          "favorite":true
        }            
    }

    if(this.isGuest){
      let fav = []
      if(this.isFavorite){                
          if(localStorage.getItem(`localFav${data.media_type}`) != undefined){
              fav = JSON.parse(localStorage.getItem(`localFav${data.media_type}`))
          }
          fav.push(data);
          localStorage.setItem(`localFav${data.media_type}`,JSON.stringify(fav))
      }
      else{
          fav = JSON.parse(localStorage.getItem(`localFav${data.media_type}`))
          for(var i = 0; i < fav.length; i++) {
              if(fav[i].media_id == data.media_id) {
                  fav.splice(i, 1);    
                  break;
              }
          }
          localStorage.setItem(`localFav${data.media_type}`,JSON.stringify(fav))
      }
    }
    else{
      this.httpService.mdbSetFavorite(data);
    }          
  }

  addToList():void{
    this.opened = !this.opened;
    this.lists = this.httpService.getCheckedLists();
  }

  get popupStyle(): any{
    return{
      'visibility' : this.opened ? 'visible' : 'hidden',
      'opacity' : this.opened ? '100' : '0'
    }
  }

  saveToList(list:ListResult): void{
    if(list.contains){
      let listToRemove = {
        "media_id": this.mediaID
      }
      this.httpService.mdbRemoveFromList(listToRemove, list.id.toString());
      this.msg = "Movie successfully removed!"
    }else{
      let listToAdd = {
        "media_id": this.mediaID,
        "list_id":list.id
      }
      this.httpService.mdbAddtoList(listToAdd);
      this.msg = "Movie successfully added!"
    }
    this.addToList();
    this.msgOpened = true;
    this.checkList();
    setTimeout(()=>{ this.msgOpened = false }, 2000)
  }

  get msgStyle(): any{
    return{
      'visibility' : this.msgOpened ? 'visible' : 'hidden',
      'opacity' : this.msgOpened ? '100' : '0'
    }
  }
}
