import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { HomeComponent } from './home/home.component';
import { SearchComponent } from './search/search.component';
import { LoginComponent } from './login/login.component';
import { AuthguardService } from './authguard.service';
import { DetailsComponent } from './details/details.component';
import { RouterExtService } from './router-ext.service';
import { ListsComponent } from './lists/lists.component';
import { AccountComponent } from './account/account.component';


const routes: Routes = [
  {path: '', component: HomeComponent, canActivate: [AuthguardService] },
  {path: 'search/:search', component: SearchComponent, canActivate: [AuthguardService] },
  {path: 'login', component: LoginComponent},
  {path: ':type/:id', component: DetailsComponent, canActivate: [AuthguardService]},
  {path: 'lists', component: ListsComponent, canActivate: [AuthguardService]},
  {path: 'account', component: AccountComponent, canActivate: [AuthguardService]}
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule],
  providers: [RouterExtService]
})
export class AppRoutingModule { }
