import { Component, OnInit } from '@angular/core';
import { IMedia, Result } from '../media';
import { HttpService } from '../http.service';
import { Router } from '@angular/router';
import { IList, ListResult, ListItem} from '../list';

@Component({
  selector: 'app-lists',
  templateUrl: './lists.component.html',
  styleUrls: ['./lists.component.scss']
})
export class ListsComponent implements OnInit {

  favMovies:Result[] = [];
  favTV:Result[] = [];
  lists:ListResult[] = [];
  listDetails:ListItem[] = [];
  listName = '';
  listDescription = '';
  isGuest:boolean;

  constructor(
    private httpService:HttpService,
    private router: Router
  ) { }

  ngOnInit():void {
    if(localStorage.getItem('userType')=='guest'){
      this.isGuest = true;
    }
    else{
      this.isGuest = false;
    }

    this.httpService.mdbFavorites('movies').subscribe(
      (content:IMedia) =>{
        this.favMovies = content['results'];
      }
    )

    this.httpService.mdbFavorites('tv').subscribe(
      (content:IMedia) =>{
        this.favTV = content['results'];
      }
    )

    this.httpService.mdbLists().subscribe(
      (content:IList) =>{
        this.lists = content.results;
      }
    )
  }

  showList(event):void{
    let objs = document.querySelectorAll(".extraInfo");
    objs.forEach(element => {
        element.classList.add("c-fav")
        element.classList.remove("extraInfo")
    });
    let obj = event.path.find(obj => {return obj.className === "c-fav"})
    obj.classList = "extraInfo"
  }

  hideList(event):void{
    let obj = event.path.find(obj => {return obj.className === "extraInfo"})
    obj.classList = "c-fav"
  }

  createList(create: HTMLElement):void{
    create.classList.add("extra");
  }

  cancelCreate(create: HTMLElement):void{
    create.classList.remove("extra");
  }

  deleteList(ListId:number){
    this.httpService.mdbDeleteList(ListId.toString());
    for(var i = 0; i < this.lists.length; i++) {
      if(this.lists[i].id == ListId) {
          this.lists.splice(i, 1);    
          break;
      }
  }
  }

  confirmCreate():void{
    if(this.listName != null){
      let list = {
        "name": this.listName,
        "description": this.listDescription,
        "language":"en"
      }
      this.httpService.mdbCreateList(list);
    }
  }

  getListDetails(event, id):void{
    this.httpService.mdbListDetails(id).subscribe(
      (content : IList) => {
        this.listDetails = content.items;
      }
    )
    this.showList(event);
  }

  showDetailsMovie(item):void{
    this.router.navigateByUrl("movie/"+item.id);
  }

  showDetailsTV(item):void{
    this.router.navigateByUrl("tv/"+item.id);
  }

}
