import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { HttpService } from '../http.service';
import { IMedia } from '../media';
import {Location} from '@angular/common';

@Component({
  selector: 'app-search',
  templateUrl: './search.component.html',
  styleUrls: ['./search.component.scss']
})
export class SearchComponent implements OnInit {

  errorMessage = '';
  search='';
  searchParam:string;
  foundMovies:IMedia[] = [];
  foundTV:IMedia[] = [];

  constructor(
    private route: ActivatedRoute,
    private httpService: HttpService,
    private location: Location,
    private router: Router
  ) { }

  ngOnInit() :void{
    this.searchParam = this.route.snapshot.paramMap.get('search');
    this.searchMedia();

    this.route.params.subscribe(params => {
      this.searchParam = params['search'];
      this.searchMedia();
  });
  }

  searchMedia():void{
    if(this.search != ''){
      this.searchParam = this.search
      this.location.replaceState("/search/" + this.searchParam);
    }
    this.httpService.mdbSearchMovies(this.searchParam).subscribe(
      (content:IMedia[]) => {
        this.foundMovies = content['results'];
      },
      error => this.errorMessage = <any>error
    );

    this.httpService.mdbSearchTVshows(this.searchParam).subscribe(
      (content:IMedia[]) => {
        this.foundTV = content['results'];
      },
      error => this.errorMessage = <any>error
    );
  }

  keyDownFunction(event) {
    if(event.keyCode == 13) {
      this.searchMedia();
    }
  }

  showDetailsMovie(item):void{
    this.router.navigateByUrl("movie/"+item.id);
  }

  showDetailsTV(item):void{
    this.router.navigateByUrl("tv/"+item.id);
  }
}
