import { Component, OnInit } from '@angular/core';
import { HttpService } from '../http.service';
import { IMedia } from '../media';
import { Router } from '@angular/router';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.scss']
})
export class HomeComponent implements OnInit {

  errorMessage = '';
  content:IMedia[] = [];
  page = 1;
  type = 'movie';
  sort = 'popular';
  opened: boolean = false;

  constructor(
    private httpService: HttpService,
    private router: Router) {  }
  
  ngOnInit(): void {
    if (sessionStorage.getItem("type") != null && sessionStorage.getItem("type") != undefined){
      this.type=sessionStorage.getItem("type")
      this.sort=sessionStorage.getItem("sort")
    }
    this.browseMedia();
  }

  onScroll() {
    this.page += 1;
    this.httpService.mdbBrowseContent(this.type, this.sort, this.page).subscribe(
      (content:IMedia[]) => {
        var newlist = this.content.concat(content['results']);
        this.content = newlist;
      },
      error => this.errorMessage = <any>error
    );
  }

  changeFilter():void{
    this.page = 1;
    //tv shows cant sort by 'upcoming' 
    if(this.sort == 'upcoming' && this.type=='tv'){
      this.sort='popular';
    }
    //movies cant sort by 'on air'
    if(this.sort == 'on_the_air' && this.type=='movie'){
      this.sort='popular';
    }
    //people cant sort by 'on air' or 'upcoming' or 'top_rated'
    if(this.type=='person'){
      if(this.sort == 'on_the_air' || this.sort=='upcoming' || this.sort=='top_rated'){
        this.sort='popular';
      }  
    }
    sessionStorage.setItem("type",this.type)
    sessionStorage.setItem("sort",this.sort)
    this.content = [];
    this.browseMedia();
  }

  browseMedia():void{
    for(this.page; this.page < 5;this.page++){
    this.httpService.mdbBrowseContent(this.type, this.sort, this.page).subscribe(
      (content:IMedia[]) => {
        var newlist = this.content.concat(content['results']);
        this.content = newlist;
      },
      error => this.errorMessage = <any>error
    );
    }
  }

  showDetails(item):void{
    this.router.navigateByUrl(this.type+"/"+item.id);
  }

  toggleSidebar():void {
    this.opened = !this.opened;
  }

  get sidebarStyle(): any {
    return {
        'visibility' : this.opened ? 'visible' : 'hidden',
        'opacity' : this.opened ? '100' : '0'
    };
}




}
