import { Component, OnInit, OnChanges } from '@angular/core';
import { HttpService } from '../http.service';
import { IAuth } from '../auth';
import { Router, ActivatedRoute } from '@angular/router';
import { IAccount } from '../account';
import { IMedia } from '../media';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.scss']
})
export class LoginComponent implements OnInit {

  errorMessage = '';

  constructor(
    private httpService: HttpService, 
    private router: Router,
    private route: ActivatedRoute,) {
      var token = this.route.snapshot.queryParamMap.get('request_token')
      if(token != null && token != undefined){
        this.httpService.mdbCreateSession(token);
        this.httpService.mdbAccountDetails().subscribe(
          (content:IAccount) => {
            localStorage.setItem('account', JSON.stringify(content))
          }
        );
      }
     }

  ngOnInit(): void {
   
  }
  
  userLogin(): void{
    this.httpService.mdbLogin().subscribe(
      (data: IAuth) => {
        window.location.href = `https://www.themoviedb.org/authenticate/${data.request_token}?redirect_to=https://margodtmiguel.github.io/login`;
      },
      error => this.errorMessage = <any>error
    )
  }

  guestLogin():void{
    this.httpService.mdbGuestLogin().subscribe(
      (data: IAuth) => {
        if(data.success == true){
          localStorage.setItem('sessionId', data.guest_session_id);
          localStorage.setItem('userType', 'guest');
          localStorage.setItem('expDate', data.expires_at);
          this.router.navigateByUrl('');
        }else{
          alert("Guest login is not working at the moment. Please try again later.")
        }
      },
      error => this.errorMessage = <any>error
    );
    
  }

}
