import { Injectable } from '@angular/core';
import { HttpClient, HttpErrorResponse, HttpHeaders } from '@angular/common/http';
import { Observable, throwError } from 'rxjs';
import { catchError, tap, map } from 'rxjs/operators';
import { IMedia } from './media';
import { IAuth } from './auth';
import { Router } from '@angular/router';
import { IMediaDetails } from './mediaDetails';
import { IAccount } from './account';
import { IList, ListResult } from './list';

@Injectable({
  providedIn: 'root'
})
export class HttpService {

  private APIKey = 'a45aaa83e08e14794623c4a29a1a884f';
  private BaseURL = 'https://api.themoviedb.org/3/';
  private sessionId = localStorage.getItem('sessionId');
  private favorites:IMedia;
  private listContains:ListResult[];

  constructor(
    private http: HttpClient,
    private router: Router
    ) {  }

  mdbBrowseContent(type:string, sort:string, page:number): Observable<IMedia[]> {
    return this.http.get<IMedia[]>(`${this.BaseURL}${type}/${sort}?api_key=${this.APIKey}&language=en-US&page=${page}`).pipe(
      catchError(this.handleError)
    );
  }

  mdbSearchMovies(param:string): Observable<IMedia[]>{
    return this.http.get<IMedia[]>(`${this.BaseURL}search/movie?api_key=${this.APIKey}&language=en-US&page=1&query=${param}`).pipe(
      catchError(this.handleError)
    );  
  }

  mdbSearchTVshows(param:string): Observable<IMedia[]>{
    return this.http.get<IMedia[]>(`${this.BaseURL}search/tv?api_key=${this.APIKey}&language=en-US&page=1&query=${param}`).pipe(
      catchError(this.handleError)
    );
  }

  mdbMediaDetails(type:string, mediaID:string): Observable<IMediaDetails>{
    return this.http.get<IMediaDetails>(`${this.BaseURL}${type}/${mediaID}?api_key=${this.APIKey}&language=en-US`).pipe(
      catchError(this.handleError)
    )
  }

  mdbFavorites(type:string): Observable<IMedia>{
    var account = <any>{};
    account = JSON.parse(localStorage.getItem('account'));

    return this.http.get<IMedia>(`${this.BaseURL}account/${account.id}/favorite/${type}?api_key=${this.APIKey}&session_id=${this.sessionId}&language=en-US&sort_by=created_at.asc&page=1`).pipe(
      tap(data => this.favorites = data),
      catchError(this.handleError)
    )
  }

  mdbLists(): Observable<IList>{
    var account = <any>{};
    account = JSON.parse(localStorage.getItem('account'));

    return this.http.get<IList>(`${this.BaseURL}account/${account.id}/lists?api_key=${this.APIKey}&session_id=${this.sessionId}`).pipe(
      catchError(this.handleError)
    )
  }

  mdbListDetails(listId): Observable<IList>{
    return this.http.get<IList>(`${this.BaseURL}list/${listId}?api_key=${this.APIKey}`).pipe(
      catchError(this.handleError)
    )
  }

  mdbCreateList(list:any){
    const httpOptions = {
      headers: new HttpHeaders({
        'Content-Type':  'application/json',
        'Accept': 'application/json'
      })
    };
    this.http.post(`${this.BaseURL}list?api_key=${this.APIKey}&session_id=${this.sessionId}`, 
      JSON.stringify(list), 
      httpOptions).subscribe(
        res => {
         location.reload();
        },
        (err: HttpErrorResponse) => {
          location.reload();
          console.log(err.error);
          console.log(err.name);
          console.log(err.message);
          console.log(err.status);
        }
      );
  }

  mdbAddtoList(listToAdd:any){
    const httpOptions = {
      headers: new HttpHeaders({
        'Content-Type': 'application/json',
        'Accept': 'application/json'
      })
    };
    this.http.post(`${this.BaseURL}list/${listToAdd.list_id}/add_item?api_key=${this.APIKey}&session_id=${this.sessionId} `, 
      JSON.stringify(listToAdd), 
      httpOptions).subscribe(
        res => {
        //  console.log(res);
        },
        (err: HttpErrorResponse) => {
          console.log(err.error);
          console.log(err.name);
          console.log(err.message);
          console.log(err.status);
        }
      );
  }

  mdbRemoveFromList(listToRemove:any, listId:string){
    const httpOptions = {
      headers: new HttpHeaders({
        'Content-Type': 'application/json',
        'Accept': 'application/json'
      })
    };
    this.http.post(`${this.BaseURL}list/${listId}/remove_item?api_key=${this.APIKey}&session_id=${this.sessionId}`, 
      JSON.stringify(listToRemove), 
      httpOptions).subscribe(
        res => {
        //  console.log(res);
        },
        (err: HttpErrorResponse) => {
          console.log(err.error);
          console.log(err.name);
          console.log(err.message);
          console.log(err.status);
        }
      );
  }

  mdbCheckIfInList(list:IList, mediaID:string){
    list.results.forEach(l=>{
      this.mdbListDetails(l.id).subscribe(
        (listDetails : IList)=>{
          listDetails.items.forEach(i =>{
            if(i.id.toString() == mediaID){
              l.contains = true;
            }
          })
        }
      )
    })
    this.listContains = list.results;
  }

  getCheckedLists(){
    return this.listContains;
  }
  
  mdbDeleteList(ListId:string){
    const httpOptions = {
      headers: new HttpHeaders({
        'Content-Type': 'application/json',
        'Accept': 'application/json'
      })
    };
    this.http.delete(`${this.BaseURL}list/${ListId}?api_key=${this.APIKey}&session_id=${this.sessionId}`, 
      httpOptions).subscribe(
        res => {
        //  console.log(res);
        },
        (err: HttpErrorResponse) => {
          // console.log(err.error);
        }
      );
  }

  isFavorite(id:number): boolean{
    var isFavorite = false;
    for(let i=0, l=this.favorites.results.length;i<l;i++){
      if(this.favorites.results[i].id == id){
        isFavorite = true;
      }
    }
    return isFavorite;
  }

  mdbSetFavorite(data:any){
    var account = <any>{};
    account = JSON.parse(localStorage.getItem('account'));

    const httpOptions = {
      headers: new HttpHeaders({
        'Content-Type':  'application/json',
        'Accept': 'application/json'
      })
    };
    this.http.post(`${this.BaseURL}account/${account.id}/favorite?api_key=${this.APIKey}&session_id=${this.sessionId}`, 
      JSON.stringify(data), 
      httpOptions).subscribe(
        res => {
        //  console.log(res);
        },
        (err: HttpErrorResponse) => {
          console.log(err.error);
          console.log(err.name);
          console.log(err.message);
          console.log(err.status);
        }
      );
  }

  mdbAccountDetails(): Observable<IAccount>{
    return this.http.get<IAccount>(`${this.BaseURL}account?api_key=${this.APIKey}&session_id=${this.sessionId}`).pipe(
      catchError(this.handleError)
    )
  }

  mdbGuestLogin(): Observable<IAuth> {
    return this.http.get<IAuth>(`${this.BaseURL}authentication/guest_session/new?api_key=${this.APIKey}`).pipe(
      catchError(this.handleError)
    );
  }

  mdbLogin(): Observable<IAuth> {
    return this.http.get<IAuth>(`${this.BaseURL}authentication/token/new?api_key=${this.APIKey}`).pipe(
      catchError(this.handleError)
    );
  }

  mdbCreateSession(token:string){
    const httpOptions = {
      headers: new HttpHeaders({
        'Content-Type':  'application/json',
        'Accept': 'application/json'
      })
    };

    let data = {"request_token":token}

    this.http.post(`${this.BaseURL}authentication/session/new?api_key=${this.APIKey}`, 
      JSON.stringify(data), 
      httpOptions).subscribe(
        res => {
          localStorage.setItem('sessionId', res['session_id']);

          var expDate = new Date(Date.now());
          expDate.setDate(expDate.getDate() + 1);
          var expDateString = expDate.toDateString();
          localStorage.setItem('expDate', expDateString);

          localStorage.setItem('userType', 'user');

          this.router.navigateByUrl('');
        },
        (err: HttpErrorResponse) => {
          console.log(err.error);
          console.log(err.name);
          console.log(err.message);
          console.log(err.status);
        }
      );
  }

  private handleError(err: HttpErrorResponse) {
    let errorMessage = '';
    if (err.error instanceof ErrorEvent) {
      errorMessage = `An error occurred: ${err.error.message}`;
    } else {
      errorMessage = `Server returned code: ${err.status}, error message is: ${err.message}`;
    }
    console.error(errorMessage);
    return throwError(errorMessage);
  }

}
